import {useState} from 'react'

const Pool = ({poolinfo, b0, b1, b2, b3, b4, b5, b6, b7, b8, b9, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {
    const [vbusd, setVBusd] = useState(0)
    const [vbeth, setVBeth] = useState(0)
    const [vauto, setVAuto] = useState(0)
    const [vxxx1, setVXxx1] = useState(0)
    const [vxxx2, setVXxx2] = useState(0)
    const [vxxx3, setVXxx3] = useState(0)
    const [vxxx4, setVXxx4] = useState(0)
    const [vxxx5, setVXxx5] = useState(0)
    const [vxxx6, setVXxx6] = useState(0)
    const [vxxx7, setVXxx7] = useState(0)
    return (
        <div>
            <h3>Pool Info</h3>
            <table>
            <tbody>
                <tr>
                    <th></th>
                    <th>BUSD</th>
                    <th>BETH</th>
                    <th>AUTO</th>
                    <th>XXX1</th>
                    <th>XXX2</th>
                    <th>XXX3</th>
                    <th>XXX4</th>
                    <th>XXX5</th>
                    <th>XXX6</th>
                    <th>XXX7</th>
                    <th>Protocol Fee Accrued</th>
                </tr>
                <tr>
                    <th>Pool</th>
                    <td>{poolinfo.balanceBUSD}</td>
                    <td>{poolinfo.balanceBETH}</td>
                    <td>{poolinfo.balanceAUTO}</td>
                    <td>{poolinfo.balanceXXX1}</td>
                    <td>{poolinfo.balanceXXX2}</td>
                    <td>{poolinfo.balanceXXX3}</td>
                    <td>{poolinfo.balanceXXX4}</td>
                    <td>{poolinfo.balanceXXX5}</td>
                    <td>{poolinfo.balanceXXX6}</td>
                    <td>{poolinfo.balanceXXX7}</td>
                    <td>{poolinfo.protocolFeeAccrued}</td>
                </tr>
                <tr>
                    <th>User</th>
                    <td>{b0.user_balance}</td>
                    <td>{b1.user_balance}</td>
                    <td>{b2.user_balance}</td>
                    <td>{b3.user_balance}</td>
                    <td>{b4.user_balance}</td>
                    <td>{b5.user_balance}</td>
                    <td>{b6.user_balance}</td>
                    <td>{b7.user_balance}</td>
                    <td>{b8.user_balance}</td>
                    <td>{b9.user_balance}</td>
                    <td></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onMint(0)}>Mint BUSD</button></td>
                    <td><button onClick={() => onMint(1)}>Mint BETH</button></td>
                    <td><button onClick={() => onMint(2)}>Mint AUTO</button></td>
                    <td><button onClick={() => onMint(3)}>Mint XXX1</button></td>
                    <td><button onClick={() => onMint(4)}>Mint XXX2</button></td>
                    <td><button onClick={() => onMint(5)}>Mint XXX3</button></td>
                    <td><button onClick={() => onMint(6)}>Mint XXX4</button></td>
                    <td><button onClick={() => onMint(7)}>Mint XXX5</button></td>
                    <td><button onClick={() => onMint(8)}>Mint XXX6</button></td>
                    <td><button onClick={() => onMint(9)}>Mint XXX7</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onApprove(0)}>Approve BUSD</button></td>
                    <td><button onClick={() => onApprove(1)}>Approve BETH</button></td>
                    <td><button onClick={() => onApprove(2)}>Approve AUTO</button></td>
                    <td><button onClick={() => onApprove(3)}>Approve XXX1</button></td>
                    <td><button onClick={() => onApprove(4)}>Approve XXX2</button></td>
                    <td><button onClick={() => onApprove(5)}>Approve XXX3</button></td>
                    <td><button onClick={() => onApprove(6)}>Approve XXX4</button></td>
                    <td><button onClick={() => onApprove(7)}>Approve XXX5</button></td>
                    <td><button onClick={() => onApprove(8)}>Approve XXX6</button></td>
                    <td><button onClick={() => onApprove(9)}>Approve XXX7</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><input type='text' value={vbusd} onChange={(e) => setVBusd(e.target.value)} /></td>
                    <td><input type='text' value={vbeth} onChange={(e) => setVBeth(e.target.value)} /></td>
                    <td><input type='text' value={vauto} onChange={(e) => setVAuto(e.target.value)} /></td>
                    <td><input type='text' value={vxxx1} onChange={(e) => setVXxx1(e.target.value)} /></td>
                    <td><input type='text' value={vxxx2} onChange={(e) => setVXxx2(e.target.value)} /></td>
                    <td><input type='text' value={vxxx3} onChange={(e) => setVXxx3(e.target.value)} /></td>
                    <td><input type='text' value={vxxx4} onChange={(e) => setVXxx4(e.target.value)} /></td>
                    <td><input type='text' value={vxxx5} onChange={(e) => setVXxx5(e.target.value)} /></td>
                    <td><input type='text' value={vxxx6} onChange={(e) => setVXxx6(e.target.value)} /></td>
                    <td><input type='text' value={vxxx7} onChange={(e) => setVXxx7(e.target.value)} /></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddLiquidity(0, vbusd)}>AddLiquidity BUSD</button></td>
                    <td><button onClick={() => onAddLiquidity(1, vbeth)}>AddLiquidity BETH</button></td>
                    <td><button onClick={() => onAddLiquidity(2, vauto)}>AddLiquidity AUTO</button></td>
                    <td><button onClick={() => onAddLiquidity(3, vxxx1)}>AddLiquidity XXX1</button></td>
                    <td><button onClick={() => onAddLiquidity(4, vxxx2)}>AddLiquidity XXX2</button></td>
                    <td><button onClick={() => onAddLiquidity(5, vxxx3)}>AddLiquidity XXX3</button></td>
                    <td><button onClick={() => onAddLiquidity(6, vxxx4)}>AddLiquidity XXX4</button></td>
                    <td><button onClick={() => onAddLiquidity(7, vxxx5)}>AddLiquidity XXX5</button></td>
                    <td><button onClick={() => onAddLiquidity(8, vxxx6)}>AddLiquidity XXX6</button></td>
                    <td><button onClick={() => onAddLiquidity(9, vxxx7)}>AddLiquidity XXX7</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveLiquidity(0, vbusd)}>RemoveLiquidity BUSD</button></td>
                    <td><button onClick={() => onRemoveLiquidity(1, vbeth)}>RemoveLiquidity BETH</button></td>
                    <td><button onClick={() => onRemoveLiquidity(2, vauto)}>RemoveLiquidity AUTO</button></td>
                    <td><button onClick={() => onRemoveLiquidity(3, vxxx1)}>RemoveLiquidity XXX1</button></td>
                    <td><button onClick={() => onRemoveLiquidity(4, vxxx2)}>RemoveLiquidity XXX2</button></td>
                    <td><button onClick={() => onRemoveLiquidity(5, vxxx3)}>RemoveLiquidity XXX3</button></td>
                    <td><button onClick={() => onRemoveLiquidity(6, vxxx4)}>RemoveLiquidity XXX4</button></td>
                    <td><button onClick={() => onRemoveLiquidity(7, vxxx5)}>RemoveLiquidity XXX5</button></td>
                    <td><button onClick={() => onRemoveLiquidity(8, vxxx6)}>RemoveLiquidity XXX6</button></td>
                    <td><button onClick={() => onRemoveLiquidity(9, vxxx7)}>RemoveLiquidity XXX7</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddMargin(0, vbusd)}>AddMargin BUSD</button></td>
                    <td><button onClick={() => onAddMargin(1, vbeth)}>AddMargin BETH</button></td>
                    <td><button onClick={() => onAddMargin(2, vauto)}>AddMargin AUTO</button></td>
                    <td><button onClick={() => onAddMargin(3, vxxx1)}>AddMargin XXX1</button></td>
                    <td><button onClick={() => onAddMargin(4, vxxx2)}>AddMargin XXX2</button></td>
                    <td><button onClick={() => onAddMargin(5, vxxx3)}>AddMargin XXX3</button></td>
                    <td><button onClick={() => onAddMargin(6, vxxx4)}>AddMargin XXX4</button></td>
                    <td><button onClick={() => onAddMargin(7, vxxx5)}>AddMargin XXX5</button></td>
                    <td><button onClick={() => onAddMargin(8, vxxx6)}>AddMargin XXX6</button></td>
                    <td><button onClick={() => onAddMargin(9, vxxx7)}>AddMargin XXX7</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveMargin(0, vbusd)}>RemoveMargin BUSD</button></td>
                    <td><button onClick={() => onRemoveMargin(1, vbeth)}>RemoveMargin BETH</button></td>
                    <td><button onClick={() => onRemoveMargin(2, vauto)}>RemoveMargin AUTO</button></td>
                    <td><button onClick={() => onRemoveMargin(3, vxxx1)}>RemoveMargin XXX1</button></td>
                    <td><button onClick={() => onRemoveMargin(4, vxxx2)}>RemoveMargin XXX2</button></td>
                    <td><button onClick={() => onRemoveMargin(5, vxxx3)}>RemoveMargin XXX3</button></td>
                    <td><button onClick={() => onRemoveMargin(6, vxxx4)}>RemoveMargin XXX4</button></td>
                    <td><button onClick={() => onRemoveMargin(7, vxxx5)}>RemoveMargin XXX5</button></td>
                    <td><button onClick={() => onRemoveMargin(8, vxxx6)}>RemoveMargin XXX6</button></td>
                    <td><button onClick={() => onRemoveMargin(9, vxxx7)}>RemoveMargin XXX7</button></td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default Pool
