import {useState} from 'react'

const Symbols = ({s0, s1, s2, s3, s4, s5, s6, s7, s8, s9, onTrade}) => {
    const [volume0, setVolume0] = useState(0)
    const [volume1, setVolume1] = useState(0)
    const [volume2, setVolume2] = useState(0)
    const [volume3, setVolume3] = useState(0)
    const [volume4, setVolume4] = useState(0)
    const [volume5, setVolume5] = useState(0)
    const [volume6, setVolume6] = useState(0)
    const [volume7, setVolume7] = useState(0)
    const [volume8, setVolume8] = useState(0)
    const [volume9, setVolume9] = useState(0)
    return (
        <div>
        <h3>Symbol Info</h3>
        <table>
        <tbody>
            <tr>
                <th>symbol</th>
                <th>BTCUSD</th>
                <th>ETHUSD</th>
                <th>XXXYYY1</th>
                <th>XXXYYY2</th>
                <th>XXXYYY3</th>
                <th>XXXYYY4</th>
                <th>XXXYYY5</th>
                <th>XXXYYY6</th>
                <th>XXXYYY7</th>
                <th>XXXYYY8</th>
            </tr>
            <tr>
                <td>price</td>
                <td>{s0.price}</td>
                <td>{s1.price}</td>
                <td>{s2.price}</td>
                <td>{s3.price}</td>
                <td>{s4.price}</td>
                <td>{s5.price}</td>
                <td>{s6.price}</td>
                <td>{s7.price}</td>
                <td>{s8.price}</td>
                <td>{s9.price}</td>
            </tr>
            <tr>
                <td>cumulativeFundingRate</td>
                <td>{s0.cumulativeFundingRate}</td>
                <td>{s1.cumulativeFundingRate}</td>
                <td>{s2.cumulativeFundingRate}</td>
                <td>{s3.cumulativeFundingRate}</td>
                <td>{s4.cumulativeFundingRate}</td>
                <td>{s5.cumulativeFundingRate}</td>
                <td>{s6.cumulativeFundingRate}</td>
                <td>{s7.cumulativeFundingRate}</td>
                <td>{s8.cumulativeFundingRate}</td>
                <td>{s9.cumulativeFundingRate}</td>
            </tr>
            <tr>
                <td>tradersNetVolume</td>
                <td>{s0.tradersNetVolume}</td>
                <td>{s1.tradersNetVolume}</td>
                <td>{s2.tradersNetVolume}</td>
                <td>{s3.tradersNetVolume}</td>
                <td>{s4.tradersNetVolume}</td>
                <td>{s5.tradersNetVolume}</td>
                <td>{s6.tradersNetVolume}</td>
                <td>{s7.tradersNetVolume}</td>
                <td>{s8.tradersNetVolume}</td>
                <td>{s9.tradersNetVolume}</td>
            </tr>
            <tr>
                <td>tradersNetCost</td>
                <td>{s0.tradersNetCost}</td>
                <td>{s1.tradersNetCost}</td>
                <td>{s2.tradersNetCost}</td>
                <td>{s3.tradersNetCost}</td>
                <td>{s4.tradersNetCost}</td>
                <td>{s5.tradersNetCost}</td>
                <td>{s6.tradersNetCost}</td>
                <td>{s7.tradersNetCost}</td>
                <td>{s8.tradersNetCost}</td>
                <td>{s9.tradersNetCost}</td>
            </tr>
            <tr>
                <td>user_volume</td>
                <td>{s0.user_volume}</td>
                <td>{s1.user_volume}</td>
                <td>{s2.user_volume}</td>
                <td>{s3.user_volume}</td>
                <td>{s4.user_volume}</td>
                <td>{s5.user_volume}</td>
                <td>{s6.user_volume}</td>
                <td>{s7.user_volume}</td>
                <td>{s8.user_volume}</td>
                <td>{s9.user_volume}</td>
            </tr>
            <tr>
                <td>user_cost</td>
                <td>{s0.user_cost}</td>
                <td>{s1.user_cost}</td>
                <td>{s2.user_cost}</td>
                <td>{s3.user_cost}</td>
                <td>{s4.user_cost}</td>
                <td>{s5.user_cost}</td>
                <td>{s6.user_cost}</td>
                <td>{s7.user_cost}</td>
                <td>{s8.user_cost}</td>
                <td>{s9.user_cost}</td>
            </tr>
            <tr>
                <td>user_lastCumulativeFundingRate</td>
                <td>{s0.user_lastCumulativeFundingRate}</td>
                <td>{s1.user_lastCumulativeFundingRate}</td>
                <td>{s2.user_lastCumulativeFundingRate}</td>
                <td>{s3.user_lastCumulativeFundingRate}</td>
                <td>{s4.user_lastCumulativeFundingRate}</td>
                <td>{s5.user_lastCumulativeFundingRate}</td>
                <td>{s6.user_lastCumulativeFundingRate}</td>
                <td>{s7.user_lastCumulativeFundingRate}</td>
                <td>{s8.user_lastCumulativeFundingRate}</td>
                <td>{s9.user_lastCumulativeFundingRate}</td>
            </tr>
            <tr>
                <td/>
                <td><input type='text' value={volume0} onChange={(e) => setVolume0(e.target.value)} /></td>
                <td><input type='text' value={volume1} onChange={(e) => setVolume1(e.target.value)} /></td>
                <td><input type='text' value={volume2} onChange={(e) => setVolume2(e.target.value)} /></td>
                <td><input type='text' value={volume3} onChange={(e) => setVolume3(e.target.value)} /></td>
                <td><input type='text' value={volume4} onChange={(e) => setVolume4(e.target.value)} /></td>
                <td><input type='text' value={volume5} onChange={(e) => setVolume5(e.target.value)} /></td>
                <td><input type='text' value={volume6} onChange={(e) => setVolume6(e.target.value)} /></td>
                <td><input type='text' value={volume7} onChange={(e) => setVolume7(e.target.value)} /></td>
                <td><input type='text' value={volume8} onChange={(e) => setVolume8(e.target.value)} /></td>
                <td><input type='text' value={volume9} onChange={(e) => setVolume9(e.target.value)} /></td>
            </tr>
            <tr>
                <td/>
                <td><button onClick={() => onTrade(0, volume0)}>Trade BTCUSD</button></td>
                <td><button onClick={() => onTrade(1, volume1)}>Trade ETHUSD</button></td>
                <td><button onClick={() => onTrade(2, volume2)}>Trade XXXYYY1</button></td>
                <td><button onClick={() => onTrade(3, volume3)}>Trade XXXYYY2</button></td>
                <td><button onClick={() => onTrade(4, volume4)}>Trade XXXYYY3</button></td>
                <td><button onClick={() => onTrade(5, volume5)}>Trade XXXYYY4</button></td>
                <td><button onClick={() => onTrade(6, volume6)}>Trade XXXYYY5</button></td>
                <td><button onClick={() => onTrade(7, volume7)}>Trade XXXYYY6</button></td>
                <td><button onClick={() => onTrade(8, volume8)}>Trade XXXYYY7</button></td>
                <td><button onClick={() => onTrade(9, volume9)}>Trade XXXYYY8</button></td>
            </tr>
        </tbody>
        </table>
        </div>
    )
}

export default Symbols
