import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import Pool from './components/Pool'
import Symbols from './components/Symbols'
import BTokens from './components/BTokens'
import Liquidation from './components/Liquidation'

function fromWei(value, unit='ether') {
    return Web3.utils.fromWei(value, unit)
}

function toWei(value, unit='ether') {
    return Web3.utils.toWei(value, unit)
}

function App() {

    const web3 = new Web3(window.ethereum)

    const busd = new web3.eth.Contract(abis.erc20, '0x4038191eFb39Fe1d21a48E061F8F14cF4981A0aF')
    const beth = new web3.eth.Contract(abis.erc20, '0x5f59b256e411CB222D1790a08De492f4b6dA6E62')
    const auto = new web3.eth.Contract(abis.erc20, '0xd2F37BADdB702FF778aA038C63b7068054d93508')

    const xxx1 = new web3.eth.Contract(abis.erc20, '0x5ca976F1473d471e7aeDb28266Dc7631577f6a13')
    const xxx2 = new web3.eth.Contract(abis.erc20, '0x0E7593F7058958dD3c6D487f0Bcb4F5455B64B67')
    const xxx3 = new web3.eth.Contract(abis.erc20, '0x114B9AF81131C72a57127dFCbCEb1cFEBDA55Bd4')
    const xxx4 = new web3.eth.Contract(abis.erc20, '0x56EA96031aa2BFe3A2315a4731b4C799bFda93f0')
    const xxx5 = new web3.eth.Contract(abis.erc20, '0x4538C10ECB070123F531D13C55d231bC2FdDeba5')
    const xxx6 = new web3.eth.Contract(abis.erc20, '0x7c78Fb782bf32A6Fbe74B120BA77817258dB9c2c')
    const xxx7 = new web3.eth.Contract(abis.erc20, '0xE91347f6abA2240d78a8B021B31904dCb08bcFBe')

    const ltoken = new web3.eth.Contract(abis.ltoken, '0x61162b0c9665Ce27a53b59E79C1B7A929cc3bB57')
    const ptoken = new web3.eth.Contract(abis.ptoken, '0xeBA1c76F7A773B8210130f068798839F84392241')
    const router = new web3.eth.Contract(abis.router, '0xaDEe3A9149ee1FBa712aB081c5A6067D613571C1')
    const pool   = new web3.eth.Contract(abis.pool,   '0x7dB32101081B17E105283820b2Ed3659DFE21470')

    const [data, setData] = useState({
        account: '',
        poolinfo: {},
        symbol0: {},
        symbol1: {},
        btoken0: {},
        btoken1: {},
        btoken2: {},

        symbol2: {},
        symbol3: {},
        symbol4: {},
        symbol5: {},
        symbol6: {},
        symbol7: {},
        symbol8: {},
        symbol9: {},

        btoken3: {},
        btoken4: {},
        btoken5: {},
        btoken6: {},
        btoken7: {},
        btoken8: {},
        btoken9: {},
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = await getAccount()
        const poolinfo = await getPool()
        const symbol0 = await getSymbol(account, 0)
        const symbol1 = await getSymbol(account, 1)
        const symbol2 = await getSymbol(account, 2)
        const symbol3 = await getSymbol(account, 3)
        const symbol4 = await getSymbol(account, 4)
        const symbol5 = await getSymbol(account, 5)
        const symbol6 = await getSymbol(account, 6)
        const symbol7 = await getSymbol(account, 7)
        const symbol8 = await getSymbol(account, 8)
        const symbol9 = await getSymbol(account, 9)
        const btoken0 = await getBToken(account, 0)
        const btoken1 = await getBToken(account, 1)
        const btoken2 = await getBToken(account, 2)
        const btoken3 = await getBToken(account, 3)
        const btoken4 = await getBToken(account, 4)
        const btoken5 = await getBToken(account, 5)
        const btoken6 = await getBToken(account, 6)
        const btoken7 = await getBToken(account, 7)
        const btoken8 = await getBToken(account, 8)
        const btoken9 = await getBToken(account, 9)
        setData({
            account,
            poolinfo,
            symbol0,
            symbol1,
            symbol2,
            symbol3,
            symbol4,
            symbol5,
            symbol6,
            symbol7,
            symbol8,
            symbol9,
            btoken0,
            btoken1,
            btoken2,
            btoken3,
            btoken4,
            btoken5,
            btoken6,
            btoken7,
            btoken8,
            btoken9
        })
    }

    const getAccount = async () => {
        return Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
    }

    const getPool = async () => {
        return {
            balanceBUSD: fromWei(await busd.methods.balanceOf(pool.options.address).call()),
            balanceBETH: fromWei(await beth.methods.balanceOf(pool.options.address).call()),
            balanceAUTO: fromWei(await auto.methods.balanceOf(pool.options.address).call()),
            balanceXXX1: fromWei(await xxx1.methods.balanceOf(pool.options.address).call()),
            balanceXXX2: fromWei(await xxx2.methods.balanceOf(pool.options.address).call()),
            balanceXXX3: fromWei(await xxx3.methods.balanceOf(pool.options.address).call()),
            balanceXXX4: fromWei(await xxx4.methods.balanceOf(pool.options.address).call()),
            balanceXXX5: fromWei(await xxx5.methods.balanceOf(pool.options.address).call()),
            balanceXXX6: fromWei(await xxx6.methods.balanceOf(pool.options.address).call()),
            balanceXXX7: fromWei(await xxx7.methods.balanceOf(pool.options.address).call()),
            protocolFeeAccrued: fromWei(await pool.methods.getProtocolFeeAccrued().call()),
        }
    }

    const getSymbol = async (address, symbolId) => {
        const s = await pool.methods.getSymbol(symbolId).call()
        const p = await ptoken.methods.getPosition(address, symbolId).call()
        return {
            symbol: s.symbol,
            price: fromWei(s.price),
            cumulativeFundingRate: fromWei(s.cumulativeFundingRate),
            tradersNetVolume: fromWei(s.tradersNetVolume),
            tradersNetCost: fromWei(s.tradersNetCost),
            user_volume: fromWei(p.volume),
            user_cost: fromWei(p.cost),
            user_lastCumulativeFundingRate: fromWei(p.lastCumulativeFundingRate),
        }
    }

    const getBTokenFromId = (bTokenId) => {
        if (bTokenId === 0) return busd
        else if (bTokenId === 1) return beth
        else if (bTokenId === 2) return auto
        else if (bTokenId === 3) return xxx1
        else if (bTokenId === 4) return xxx2
        else if (bTokenId === 5) return xxx3
        else if (bTokenId === 6) return xxx4
        else if (bTokenId === 7) return xxx5
        else if (bTokenId === 8) return xxx6
        else if (bTokenId === 9) return xxx7
    }

    const getBToken = async (address, bTokenId) => {
        const b = await pool.methods.getBToken(bTokenId).call()
        const l = await ltoken.methods.getAsset(address, bTokenId).call()
        return {
            symbol: await getBTokenFromId(bTokenId).methods.symbol().call(),
            price: fromWei(b.price),
            liquidity: fromWei(b.liquidity),
            pnl: fromWei(b.pnl),
            cumulativePnl: fromWei(b.cumulativePnl),
            user_balance: fromWei(await getBTokenFromId(bTokenId).methods.balanceOf(address).call()),
            user_liquidity: fromWei(l.liquidity),
            user_pnl: fromWei(l.pnl),
            user_lastCumulativePnl: fromWei(l.lastCumulativePnl),
            user_margin: fromWei(await ptoken.methods.getMargin(address, bTokenId).call()),
        }
    }

    const onMint = async (id) => {
        if (id === 0) {
            await busd.methods.mint(data.account, '10000000000000000000000').send({from: data.account})
        } else if (id === 1) {
            await beth.methods.mint(data.account, '10000000000000000000').send({from: data.account})
        } else if (id === 2) {
            await auto.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 3) {
            await xxx1.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 4) {
            await xxx2.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 5) {
            await xxx3.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 6) {
            await xxx4.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 7) {
            await xxx5.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 8) {
            await xxx6.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        } else if (id === 9) {
            await xxx7.methods.mint(data.account, '1000000000000000000000').send({from: data.account})
        }
        await update()
    }

    const onApprove = async (id) => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        if (id === 0) {
            await busd.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 1) {
            await beth.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 2) {
            await auto.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 3) {
            await xxx1.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 4) {
            await xxx2.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 5) {
            await xxx3.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 6) {
            await xxx4.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 7) {
            await xxx5.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 8) {
            await xxx6.methods.approve(pool.options.address, max).send({from: data.account})
        } else if (id === 9) {
            await xxx7.methods.approve(pool.options.address, max).send({from: data.account})
        }
    }

    const onAddLiquidity = async (id, value) => {
        await router.methods.addLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveLiquidity = async (id, value) => {
        await router.methods.removeLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onAddMargin = async (id, value) => {
        await router.methods.addMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveMargin = async (id, value) => {
        await router.methods.removeMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onTrade = async (id, value) => {
        await router.methods.trade(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onLiquidate = async (address) => {
        await router.methods.liquidate(address).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV2 BSC Testnet</h1>
            <Pool
                poolinfo={data.poolinfo}
                b0={data.btoken0}
                b1={data.btoken1}
                b2={data.btoken2}
                b3={data.btoken3}
                b4={data.btoken4}
                b5={data.btoken5}
                b6={data.btoken6}
                b7={data.btoken7}
                b8={data.btoken8}
                b9={data.btoken9}
                onMint={onMint}
                onApprove={onApprove}
                onAddLiquidity={onAddLiquidity}
                onRemoveLiquidity={onRemoveLiquidity}
                onAddMargin={onAddMargin}
                onRemoveMargin={onRemoveMargin}
                onTrade={onTrade}
                onLiquidate ={onLiquidate}
            />
            <Symbols
                s0={data.symbol0}
                s1={data.symbol1}
                s2={data.symbol2}
                s3={data.symbol3}
                s4={data.symbol4}
                s5={data.symbol5}
                s6={data.symbol6}
                s7={data.symbol7}
                s8={data.symbol8}
                s9={data.symbol9}
                onTrade={onTrade}
            />
            <BTokens
                b0={data.btoken0}
                b1={data.btoken1}
                b2={data.btoken2}
                b3={data.btoken3}
                b4={data.btoken4}
                b5={data.btoken5}
                b6={data.btoken6}
                b7={data.btoken7}
                b8={data.btoken8}
                b9={data.btoken9}
            />
            <Liquidation onLiquidate={onLiquidate} />
        </div>
    )

}

export default App

